  background-repeat: no-repeat;
}

.header__text-desc {
  padding-left: 32px;
  font-size: 20px;
  text-shadow: 0 4px 10px rgba(255, 255, 255, 0.25);
}

.services-title {
  position: absolute;
  bottom: -130px;
  max-width: 864px;
