  top: 50%;
  left: 50%;
  transform: translate(35%, -50%);
  display: block;
}

.services__desc span {
  background-color: #FFF;
  color: #000;
  padding: 0px 5px 4px;
}

.feedback {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 40px;
  left: 355px;
  width: 530px;
  height: 530px;
  border-radius: 50%;
  background-image: linear-gradient(146.57deg, rgba(255, 255, 255, 0.28) 14.73%, rgba(255, 255, 255, 0.81) 65.62%);
}

.feedback-wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 450px;
  height: 450px;
  border-radius: 50%;
  border: 20px solid rgba(243, 238, 233, 0.83);
  background-color: #fff;
  z-index: 2;
}

.feedback__icon {
  width: 55px;
  height: 55px;
  background-image: url("../img/svg/feedback_icon.svg");
}

.feedback__title {
  width: 328px;
  font-family: 'MontserratAlternates-SemiBold';
  line-height: normal;
  font-size: 21px;
  text-align: center;
  color: #322823;
}

.feedback__form {
  top: -10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 200px;
  justify-content: space-around;
}

.feedback__input {
  position: relative;
  border: none;
  font-size: 19px;
}

.feedback__input::after {
  content: '';
  position: absolute;
  bottom: 2px;
  left: 0;
  width: 343px;
  height: 3px;
  background-color: rgba(50, 40, 35, 0.7);
  border-radius: 50px;
}

.feedback__input-input {
  position: relative;
  width: 343px;
  height: 39px;
  border: none;
  background-image: url("../img/svg/feedback_input_bg.svg");
  background-repeat: no-repeat;
  background-position: 100%, 50%;
}

.feedback__input-input:focus {
  outline-color: transparent;
}

.btn {
  padding: 14px 46px;
  background: linear-gradient(90deg, #f48922 0, #ffb015 100%);
  border: none;
  border-radius: 50px;
  font-size: 19px;
  color: #fff;
  box-shadow: 0 8px 35px rgba(254, 171, 23, 0.7);
  cursor: pointer;
  transition: all .9s;
}

.btn:hover {
  background: linear-gradient(90deg, #f17c0c 0, #fba600 100%), 50%;
}

.checkbox {
  position: relative;
  display: flex;
  align-items: center;
}

.checkbox input[type=checkbox] {
  visibility: hidden;
}

.checkbox__label {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 20px;
  min-width: 20px;
  width: 21px;
  height: 21px;
  background-color: transparent;
  border: 1px solid #322823;
  border-radius: 50%;
  cursor: pointer;
}

.checkbox__label::after {
  content: "";
  position: absolute;
  top: 6px;
  left: 4px;
  width: 10px;
  height: 4px;
  border: 2px solid #322823;
  border-top: none;
  border-right: none;
  opacity: 1;
  transform: rotate(-45deg);
}

.checkbox input[type=checkbox]:checked + label:after {
  opacity: 1;
}

.checkbox__label:after {
  position: absolute;
  top: 6px;
  left: 4px;
  width: 10px;
  height: 4px;
  border: 2px solid #322823;
  border-top: none;
  border-right: none;
  content: "";
  opacity: 0;
  transform: rotate(-45deg);
}

.checkbox__desc_round-feedback {
  padding-left: 14px;
  width: 190px;
  height: 48px;
  font-size: 13px;
  margin-top: -5px;
}

.checkbox__desc {
  padding-left: 10px;
  font-weight: 200;
  line-height: 16px;
  font-size: 13px;
  color: #322823;
}

.checkbox__desc span {
  cursor: pointer;
}

.checkbox__desc span:hover {
  border-bottom: 1px solid #232323;
}

.btn-mobile {
  display: none;
}

/* header-end */
/* success */
.success {
  position: relative;
  padding: 110px 181px 50px 181px;
  margin-top: 100px;
  background-image: url("../img/svg/big_round_decor.svg");
  background-repeat: no-repeat;
  background-position: center -65px;
}

.success::before {
  content: '';
  position: absolute;
  left: 0;
  top: -150px;
  width: 204px;
  height: 834px;
  background-image: url("../img/decor/success_left_decor.png");
  background-repeat: no-repeat;
}

.success::after {
  content: '';
  position: absolute;
  right: 0;
  top: 180px;
  width: 267px;
  height: 800px;
  background-image: url("../img/decor/success_right_decor.png");
  background-repeat: no-repeat;
  z-index: -1;
}

.success .row {
  height: 100%;
  min-height: 95px;
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  margin-bottom: 10px;
}

.success .row:nth-child(odd) {
  display: flex;
  justify-content: center;
}

.wrapper_small {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 986px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 12px;
  padding-right: 12px;
  z-index: 1;
}

.success-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 188px;
  font-size: 19px;
  font-weight: 300;
  text-align: center;
  color: #000;
  z-index: 0;
}

.success-item__icon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-image: url("../img/svg/item_icon.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: contain;
  cursor: pointer;
}

.success-item__icon-inner {
  position: relative;
  display: flex;
  visibility: visible;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 58px;
  height: 58px;
  opacity: 0;
  transition: .2s;
  background: linear-gradient(90deg, #f48922 0, #ffb015 100%);
}

.success-item__icon:hover .success-item__icon-inner {
  opacity: 1;
}

.success-item__icon:hover .success-item-popup {
  visibility: visible;
  opacity: 1;
}

.success-item__icon span {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  font-size: 24px;
  font-weight: 900;
}

.success-item-popup {
  position: absolute;
  bottom: 90px;
  visibility: collapse;
  width: 423px;
  padding: 10px 20px 10px;
  font-style: normal;
  font-weight: 300;
  line-height: 25px;
  font-size: 19px;
  text-align: left;
  transform: translate3d(0, -5px, 0);
  opacity: .1;
  background-color: #FBFAF8;
}

.success-item-popup::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 3px solid #EF8926;
  border-radius: 10px;
  z-index: -1;
}

.section-title {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  font-family: 'MontserratAlternates-Bold';
  text-align: center;
  font-size: 30px;
  line-height: 35px;
  color: #322823;
  z-index: 0;
}

.section-title span {
  font-family: 'MontserratAlternates-Regular';
}

.section-title::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: -1;
  background-repeat: no-repeat;
}

.success-title {
  max-width: 391px;
  line-height: 45px;
}

.success-title::before {
  margin-left: -108px;
  margin-top: -110px;
  width: 216px;
  height: 220px;
  background-image: url("../img/svg/title.svg");
  background-repeat: no-repeat;
}

.success-item__desc {
  width: 188px;
  padding-top: 10px;
}

