.accordion {
  width: 100%;
  max-width: 1200px;
  min-height: 0;
  display: block;
  position: relative;
  margin: 0 auto;
  padding: 0;
}

.accordion h2 {
  margin-top: 15px;
  margin-bottom: 15px;
}

.accordion h2::before {
  content: "";
  background-image: url("../img/svg/down-arrow.svg");
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translate(-50%, -50%);
}

.accordion-item {
  position: relative;
  margin-bottom: 20px;
}

.accordion-item__triger {
  position: relative;
  font-size: 20px;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 27px;
  display: block;
  margin-top: 30px;
  padding: 16px 45px 16px 50px;
  cursor: pointer;
  color: #322823;
  background: #fff;
  box-shadow: 0 4px 70px rgba(224, 216, 205, 0.1), 0 4px 30px rgba(224, 216, 205, 0.1);
  border: 1px solid #E7E7E7;
  border-radius: 50px;
}

.accordion-item__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.accordion-item__content {
  position: relative;
  top: -12px;
  margin-top: 0;
  padding: 20px;
  font-size: 18px;
  display: none;
  transform: translate(0, 50%);
  transition: .3s ease-in-out;
  z-index: 10;
  background: #fff;
  transition: all 0.9s;
  animation: showContent 1s linear forwards;
  border-radius: 0 0 30px 30px;
  border: 1px solid #E7E7E7;
}

.accordion-item__content p:first-child {
  margin: 0;
}

@keyframes showContent {
  0% {
    opacity: 0;
  }
  50% {
    opacity: .5;
  }
  100% {
    opacity: 1;
  }
}

.accordion-item__input:checked ~ .accordion-item__triger {
  border-radius: 30px 30px 0 0;
}

.accordion-item__input:checked ~ .accordion-item__content {
  display: block;
  transform: translateY(10px);
}

