}

.popup-menu__list ul {
  list-style-type: none;
}

.popup-menu__list ul li {
  text-align: right;
  font-size: 18px;
  letter-spacing: 1px;
  text-decoration: none;
  margin-top: 20px;
}

.popup-menu__list ul li a {
  position: relative;
}

.popup-menu__list ul li a::before {
  content: '';
  position: absolute;
  top: 50%;
  left: -45px;
  width: 0px;
  height: 2px;
  background: #fff;
  transition: all .1s;
}

.popup-menu__list ul li a:hover {
  color: rgba(255, 255, 255, 0.6);
}

.popup-menu__list ul li a:hover::before {
  width: 20px;
}

.popup-menu__list ul li:first-child {
  margin-top: 0px;
}

.menu__copy {
  display: flex;
  width: 110px;
  height: 105%;
  margin-top: 150px;
  padding-right: 44px;
  writing-mode: vertical-rl;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.5);
}

/* popup-end */
/* arrow-top */
.arrow-top {
  display: block;
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 100;
}

.arrow-top a {
  border: 2px solid #F7941F;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  box-shadow: 0 8px 35px rgba(254, 171, 23, 0.7);
  position: relative;
  transition: all .3s;
}

.arrow-top a svg {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #F7941F;
}

.arrow-top a:hover {
  box-shadow: none;
  background-color: #F7EBDD;
}

/* arrow-top-end */
